import React, { Component } from 'react'
import MovieInfo from './componets/MovieInfo'
import SeatSelect from './componets/SeatSelect'
import CanvasSeats from './componets/CanvasSeats'
import './index.css'
export default class Seat extends Component {
  state={
    selectSeat: []
  }

  addSeat = (seat) => {
    this.setState(prevState => ({
      selectSeat :[...prevState.selectSeat,seat]
    }))
  }

  removeSeat = (id) => {
    this.setState({
      selectSeat:this.state.selectSeat.filter(seat => seat.id !== id)
    })
  }

  render() {
    const {selectSeat} = this.state;

    return (
      <div className='seat'>
        <div className="tOperator">
            <div className="tOperator__icon tOperator__icon--back" onClick={() => {window.history.back() }} />
            万达影院
            <div className="tOperator__icon tOperator__icon--share" />
          </div>
          <MovieInfo/>
          <div className="seat__main">
            <div className="seat__tip"></div>
            <div className="seat__graph">
              <div className="seat__screen">#2屏幕</div>
              <CanvasSeats data= {selectSeat} onAdd={this.addSeat} onRemove = {this.removeSeat}/>
            </div>
          </div>
          <SeatSelect data= {selectSeat} onRemove ={this.removeSeat}/>
      </div>
    )
  }
}



