import React, { Component } from 'react'
// import PropTypes from 'prop-types'
import LineLink from '../../components/LineLink'
import TabMenu from '../../components/TabMenu'

import './User.css'
// import { url } from 'inspector';

export default class User extends Component {


  render() {
    return (
      <div className='user'>
        <div className="user__top">
          <div className="tOperator">
            <div className="tOperator__icon tOperator__icon--setting"></div>
            <div className="tOperator__icon tOperator__icon--bell"></div>
          </div>
          <div className="user__info">
            <div className="user__avatar" style={{ backgroundImage: 'url(/source/image/sun.png)' }}></div>
            <div className="user-detail">
              <div className="user__name">yizlee</div>
              <div>
                <span className="user__fellow">关注 11</span>
                <span className="user__fans">被关注 11</span>
              </div>
              <div className="user__level">钻石会员</div>
            </div>
          </div>
        </div>
        <div className="user__content">
        <LineLink title='收藏的电影' extra='20' />
        <LineLink title='看过的电影' extra='20' />
        </div>
        <TabMenu current='user'/>

      </div>
    )
  }
}



