import React, { Component } from 'react'
import request from '../../../helpers/request'
import ScoreDistribute from './i-components/ScoreDistribute'
import TagList from './i-components/TagList'
import CommentList from './i-components/CommentList'

export default class Comment extends Component {
  state = {
    tags: [],
    comments: [],
    current: ''
  }

  componentDidMount() {
    this.getData()
  }
  getData = async () => {
    const { tags, list } = await request('/comment');
    this.setState({
      tags,
      comments: list,
      current: tags[0] ? tags[0].text : ''
    })
  }

  changeTag = value =>{
      this.setState({
        current : value
      })
  }

  toggleZan = id => {
    this.setState((prevState) => ({
      comments: prevState.comments.map(comment => {
        if(comment.id === id) {
          return {
            ...comment,
            isZan: !comment.isZan,
          }
        }
        return {...comment}
      })
    }))
  }

  render() {
    const { tags, comments, current } = this.state;
    const filterComments = comments.filter(item => item.tag === current);

    return (
      <div className='myComment'>
        <ScoreDistribute />
        <TagList data={tags} current={current} onClick={this.changeTag} />
        <CommentList data={filterComments} onClickZan={this.toggleZan} />


      </div>
    )
  }
}



