import React from 'react'
import PropTypes from 'prop-types'

import './SeatSelect.css'

function SeatSelect({ data, onRemove }) {
  if(!data.length){
    return null;
  }

  return (
    <div className='seatSelected'>
      <ul className="seatSelected__list">
        {
          data.map(seat => (
            <li key={seat.id} className="seatSelected__item movieTicket">
              <div className="movieTicket__detail">
                <div className="movieTicket__pos">{seat.rowIndex}排{seat.colIndex}座</div>
                <div className="movieTicket__price">33元</div>
              </div>
              <div className="movieTicket__close" onClick={() => onRemove(seat.id)}>x</div>
            </li>
          ))
        }



      </ul>
      <div className="seatSelected__buy">共{33*data.length}元 确认选座</div>

    </div>
  )
}
SeatSelect.propTypes = {
  data: PropTypes.array.isRequired,
  onRemove:PropTypes.func
}
export default SeatSelect



