import React from 'react'
import Star from '../compoments/Star'
import './ScoreSummary.css'

function ScoreSummary() {
  return (
    <div className='scoreSummary'>
      <div className="scoreSummary__top">
        <div className="summaryItem">
          <div className="summaryItem__value">9.3<Star value={4}/> </div>
          <div className="summaryItem__title">观众评分13414人</div>
        </div>
        <div className="summaryItem">
          <div className="summaryItem__value">80%</div>
          <div className="summaryItem__title">推荐</div>
        </div><div className="summaryItem">
          <div className="summaryItem__value">79897</div>
          <div className="summaryItem__title">想看人数</div>
        </div>
      </div>
      <div className="summaryBtn">
        <div className="summaryBtn__item summaryBtn__item--want">想看</div>
        <div className="summaryBtn__item summaryBtn__item--seen">看过</div>

      </div>
    </div>
  )
}

export default ScoreSummary



