import React from 'react'
import './BaseInfo.css'
function BaseInfo() {
  return (
    <div className='baseInfo'>
      <div className="baseInfo__detail">
        <h3 className="baseInfo__name">唐人街探案3</h3>
        <div className="baseInfo__subTitle">Detective Chinatown</div>
        <div className="baseInfo__other">喜剧 / 动作 / 剧情</div>
        <div className="baseInfo__other">中国大陆 / 130分钟</div>
        <div className="baseInfo__other">2018年上映</div>

      </div>
      <div className="baseInfo__poster" style={{backgroundImage: 'url(/source/movie/4.jpeg)'}}></div>
    </div>
  )
}

export default BaseInfo



