import React, { Component } from 'react'
import ReactDOM from 'react-dom'
import PropTypes from 'prop-types'
import './CollpasibleText.css'

class CollpasibleText extends Component {


  state  ={
    isCollapse: false,
    isNeedCollapse: false
  }

  static PropTypes ={
    height:84

  }


  componentDidMount(){
    const dom = ReactDOM.findDOMNode(this);
    const value = this.props.height;
    if(dom.clientHeight > value){
      this.setState({
        isCollapse : true,
        isNeedCollapse: true

      })
    }
  }

  toggleStatus = () => {
    if(this.state.isNeedCollapse){
      this.setState((prevState) => ({
        isCollapse : !prevState.isCollapse
      }))
    }
  }

  render() {
    const {isCollapse} = this.state;
    const cls = isCollapse? 'collpasibleText--collapse' : '';
    const maxHeight = isCollapse? this.props.height : 'none';

    return (
      <div className={`collpasibleText ${cls}`} style={{ maxHeight:maxHeight }} onClick={this.toggleStatus} >
        {this.props.children}
        {this.state.isCollapse && <div className='collpasibleText__label'>展开</div> }
      </div>
    )
  }
}
CollpasibleText.propTypes = {
  children: PropTypes.any,
  height: PropTypes.number
}
export default CollpasibleText



