import React, { Component } from 'react'
import PropTypes from 'prop-types'
import request from '../../../helpers/request'

const SEAT_WIDTH = 56;
const SEAT_HEIGHT = 53;
const ratio = window.devicePixelRatio;
const DRAW_SEAT_WIDTH = SEAT_WIDTH * ratio;
const DRAW_SEAT_HEIGHT = SEAT_HEIGHT * ratio;
const WIDTH = SEAT_WIDTH * 10;
const HEIGHT = SEAT_HEIGHT * 6;
const CANVAS_WIDTH = WIDTH * ratio;
const CANVAS_HEIGHT = HEIGHT * ratio;


export default class CanvasSeats extends Component {


  componentDidMount(){

    this.ctx = this.refs.canvas.getContext('2d');
    this.ctx.font = `${10 * ratio}px Arial`;
    this.ctx.fillStyle = "#fff";
    this.ctx.textAlign = "center";
    const emptyImage = new Image();
    const selectImage = new Image();
    const soldImage = new Image();
    let count = 0;

    const loadCallback = () => {

      count++;
      if(count === 3){
        this.emptyImage = emptyImage;
        this.selectImage = selectImage;
        this.soldImage = soldImage;
        this.getData()

      }

    };
    emptyImage.onload = loadCallback;
    selectImage.onload = loadCallback;
    soldImage.onload = loadCallback;


    emptyImage.src = './source/seat.png';
    selectImage.src = './source/seat-green.png';
    soldImage.src = "./source/seat-red.png"



  }

  componentDidUpdate(){
    this.ctx.clearRect(0, 0, CANVAS_WIDTH, CANVAS_HEIGHT);
    this.drawSeat();
    this.drawSelectSeat();
  }

  getData = async () => {
    const  data  = await request('/seat');
    this.seat = data;
    this.drawSeat();

  }

  drawSeat = () => {
    const seatData = this.seat;
    for(let i = 0; i <seatData.length;i++){
      const { isSold, xPos, yPos } = seatData[i];
      const offsetLeft = (xPos - 1) * DRAW_SEAT_WIDTH;
      const offsetTop = (yPos - 1) * DRAW_SEAT_HEIGHT;
      if(isSold){
        this.ctx.drawImage(this.soldImage, offsetLeft,offsetTop,DRAW_SEAT_WIDTH,DRAW_SEAT_HEIGHT);
      }else{
        this.ctx.drawImage(this.emptyImage, offsetLeft,offsetTop,DRAW_SEAT_WIDTH,DRAW_SEAT_HEIGHT);

      }
    }
  }

  drawSelectSeat = () => {
    const{ data } = this.props;
    for(let i = 0; i < data.length; i++){
      const { xPos, yPos, rowIndex, colIndex } = data[i];
      const offsetLeft = (xPos - 1) * DRAW_SEAT_WIDTH;
      const offsetTop = (yPos - 1) * DRAW_SEAT_HEIGHT;
      this.ctx.drawImage(this.selectImage, offsetLeft,offsetTop,DRAW_SEAT_WIDTH,DRAW_SEAT_HEIGHT);
      this.ctx.fillText(`${rowIndex} 排`, offsetLeft + DRAW_SEAT_WIDTH / 2, offsetTop + DRAW_SEAT_HEIGHT / 2.5);
      this.ctx.fillText(`${colIndex} 座`, offsetLeft + DRAW_SEAT_WIDTH / 2, offsetTop + DRAW_SEAT_HEIGHT * 2 / 3);

    }
  }

  clickSeat = (e) => {
    const canvasOffset = this.refs.canvas.getBoundingClientRect();
    const clickX = e.pageX - canvasOffset.left;
    const clickY = e.pageY - canvasOffset.top;
    const xPos = Math.ceil(clickX / SEAT_WIDTH);
    const yPos = Math.ceil(clickY / SEAT_HEIGHT);
    const seat = this.seat.find(item => item.xPos === xPos && item.yPos === yPos);
    if (!seat || seat.isSold) {
      return;
    }

    const seatIndex = this.props.data.findIndex( item => item.id === seat.id);
    if (seatIndex >= 0) {
      this.props.onRemove(seat.id);
    } else {
      if (this.props.data.length >= 4) {
        alert('不能超过四个座位！');
      } else {
        this.props.onAdd(seat);
      }
    }
  }

  render() {
    return (

      <canvas onClick={this.clickSeat} ref='canvas' style={{ width: WIDTH, height: HEIGHT }} width={CANVAS_WIDTH} height={CANVAS_HEIGHT}>


      </canvas>
    )
  }
}

CanvasSeats.propTypes = {
  data: PropTypes.array.isRequired,
  onAdd: PropTypes.func,
  onRemove: PropTypes.func,
};



